.main {
  width: 100%;
  height: 100vh;
  position: relative;
}

.main-about {
  width: 100%;
  height: 70vh;
  position: relative;
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.main-text {
  position: absolute;
  top: 55%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
}

.main-text h1 {
  font-size: 3rem;
  font-weight: 800;
  background: white;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.main-text p {
  font-size: 1.3rem;
  color: white;
  padding: 0.5rem 0 3rem 0;
}

.main-text .show {
  text-decoration: none;
  background: white;
  padding: 1rem 2rem;
  border-radius: 6px;
  font-weight: bold;
  letter-spacing: 2px;
  color: #3a3a40;
}

@media screen and (max-width: 555px) {
  .main-text h1 {
    padding: 10px 20px;
  }

  .main-text p {
    font-size: 1.1rem;
    padding: 0 0 2rem 0;
  }

  .main-text .show {
    padding: 0.6rem 1.1rem;
    border-radius: 6px;
    font-size: 1rem;
  }
}
