.footer {
  padding: 4rem 6rem;
  background-color: #3a3a40;
  color: white;
}

.top {
  display: flex;
  align-items: center;
  text-align: start;
  justify-content: space-between;
  flex-wrap: wrap;
}

.top i {
  color: white;
  font-size: 2rem;
  margin-left: 1rem;
}

.top i:hover {
  color: #664859;
}

.bottom {
  padding-top: 2rem;
  text-align: start;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.bottom div {
  display: flex;
  flex-direction: column;
}

.bottom h4 {
  font-size: 1.3rem;
  padding: 1rem 0 0.8rem 0;
}

.bottom a {
  color: white;
}

@media screen and (max-width: 850px) {
  .footer {
    padding: 4rem 2rem;
  }

  .footer a {
    margin: 1rem 1rem 0 0;
  }

  .bottom div {
    width: 50%;
  }
}
