.signupform-container {
  margin: 10rem 6rem;
  color: #3a3a40;
  border-radius: 6px;
  box-shadow: -1px 1px 62px -18px rgba(0, 0, 0, 0.19);
  padding: 4rem 1rem;
  background-color: #b2b5b7;
}

.signupform-container form {
  padding-top: 3rem;
  display: flex;
  flex-direction: column;
  width: 50%;
  margin: auto;
}

.signupform-container input {
  height: 3rem;
  padding: 0 1rem;
  margin-bottom: 2rem;
  border-radius: 0.3rem;
  border: 1px solid #3a3a40;
}

.signupform-container button {
  display: inline;
}

@media screen and (max-width: 850px) {
  .signupform-container {
    margin: 8rem 2rem;
  }
  .signupform-container form {
    padding-top: 2rem;
    width: 90%;
  }
}
